body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  background: #dcafe8;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}
header h1 {
  margin: 0;
  width: 100%;
}
header .owl {
  width: 100px;
  height: 100px;
  background: url(/static/media/sova.32daac36.png) no-repeat;
  background-size: contain;
}
header .task {
  height: 70px;
  width: 400px;
  background: #d6d6d6;
  border: solid black 3px;
  border-radius: 10px;
}
header .bull {
  width: 100px;
  height: 100px;
  background: url(/static/media/bull.92da0fbd.png) no-repeat;
  background-size: contain;
}

#map {
  display: flex;
}
#map-basics {
  width: 100%;
  height: 500px;
}
.aside {
  width: 50%;
  height: 500px;
  background: #e9e9e9;
}


